import Saks from './_base';

const BASE_URL = 'https://www.thebay.com';
const CART_URL = `${BASE_URL}/checkout/SaksBag.jsp`;
const CHECKOUT_URL = `${BASE_URL}/checkout/checkout.jsp`;

/**
Driver behavior description:
 Existing code: yes 
 Remove code: yes 
 Stackable: yes 
 Free gift support: yes
 isValid implemented: yes 
+ Pages, where extension works:
   - cart
   - review
Optional:
 Is driver SPA and add the condition to show popup: yes
 Gather codes on site pages: no
Codes testing may show negative discount in console after user log in
also after returnig from the reveiw page on the checkout 
it's merchant specific behavior after login
 */

const Driver = class Driver extends Saks {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.isStackable = true;
		this.CART_URL = CART_URL;
		this.CHECKOUT_URL = CHECKOUT_URL;
		const isOnCartPage = window.location.href.includes('SaksBag.jsp');
		this.ADD_CODE_PARAM_VALUE = isOnCartPage ? 'applyPromoSaksBag' : 'add_promo_code_service';
	}

	applyCodes() {
		return Promise.resolve();
	}
};

export default {
	name: "Hudson's Bay",
	domain: 'thebay.com',
	Driver,
	isSinglePageApp: true,
	stackable: true,
};
