import Promise from 'bluebird';
import Saks from './_base';
import bus from '../../bus';
import { format, DOM, browserUtils, log } from '../../../utils';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;
const { getCookie, setCookie } = browserUtils;

/** Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: yes
+ Free gift support: yes
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: yes (valid ones only)
+ Manually apply code: no
+ Pages, where extension works: Cart, Checkout (except shipping address) - TEMPORARILY don't support heckout due to blocking
+ Is driver SPA: yes
+ MaxCoupons: 3, // due to blocking
+ Gather codes on site pages: yes, from current page
+ Special case about the driver works:
+ We can run code testing once on the Cart for 60 minutes (site block us), then fake code testing during 1 hour
	TEMPORARILY don't support checkout due to blocking
*/

const BASE_URL = 'https://www.saksfifthavenue.com';
const CART_URL = `${BASE_URL}/checkout/SaksBag.jsp`;
const CHECKOUT_URL = `${BASE_URL}/checkout/checkout.jsp`;

const Driver = class Driver extends Saks {
	constructor() {
		super();
		this.CART_URL = `${BASE_URL}/checkout/SaksBag.jsp`;
		this.IS_CART_PAGE = window.location.href.startsWith(this.CART_URL);
		this.CHECKOUT_URL = CHECKOUT_URL;
		this.isStackable = true;
		this.TOTAL_SELECTOR = '#jsVal-grandTotal, .hbc-price-display__subtotal .hbc-price-display__summary-price';
		this.PRODUCT_ITEM_SELECTOR =
			'.widebag-item:not([id^=saveFor]), .minibag-item, .shopping-bag-item--purchase, .hbc-cart-item';
		this.APPLIED_CODE_SELECTOR =
			'.jsRemovePromo .action-link-text, .widebag-item-promotion-codes li, .shopping-bag-item__promo-code, .hbc-cart-item__promo-code';
		this.CODE_INPUT_SELECTOR = '#promoCodeEntry, [name="promoCode"]';
		this.EMPTY_CART_SELECTOR = '.shopping-bag__no-items-msg';
		this.isAccessDenied = false;
		this.GIFT_MODAL_SELECTOR = '.gift-option-dialog-modal';
		this.MERCHANT_CODE_SELECTOR = '.hbc-promo-detail :not(a)';
		this.MERCHANT_CODE_REGEX = /use code: (\w+)/i;
		this.SHIPPING_METHOD_SELECTOR = '.shipping-method';
		this.headers = {
			'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
			'sec-fetch-mode': 'cors',
			'sec-fetch-site': 'same-origin',
		};
	}

	async checkCondition() {
		const isAccessDenied = getCookie('isAccessDenied') === 'true';
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isCart = window.location.href.startsWith(CART_URL);
		const cartEmptyEl = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isCartEmpty = cartEmptyEl && cartEmptyEl.offsetParent;
		const isGiftModal = document.querySelector(this.GIFT_MODAL_SELECTOR);
		const isOnUSLocation = getCookie('E4X_COUNTRY') === 'US';
		const totalEelement = extractElement(this.TOTAL_SELECTOR);
		const isTotal = totalEelement && elemToFloat(totalEelement) > 0;

		return !isGiftModal && !isAccessDenied && isCodeInput && isCart && !isCartEmpty && isOnUSLocation && isTotal;
	}

	getMerchantCodes() {
		return [
			...new Set(
				[...document.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
					.map(el => el.innerText.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(el => el[1]),
			),
		];
	}

	removeCodes(codes) {
		if (getCookie('appliedOnCart')) {
			return Promise.resolve();
		}
		return Promise.each(codes, code => this._removeCode(code))
			.then(() => this._getUpdatedCart())
			.then(responseJSON => ({
				originalPrice: this.parseTotal(responseJSON),
				originalItemsCount: this.parseItemsCount(responseJSON),
			}));
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
				isValid: true,
			};
			if (getCookie('appliedOnCart')) {
				log(`Codes testing forbidden!`);
				result.isValid = false;
				return Promise.delay(1500).then(() => bus.$emit('code-checked', result));
			}
			return this._applyCode(code).then(response => {
				let cartResponse = response;
				if (cartResponse.errors) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				if (typeof cartResponse !== 'object') {
					cartResponse = this._getUpdatedCart();
				}
				result.finalPrice = this.parseTotal(cartResponse);
				result.finalDiscount = originalPrice - result.finalPrice;
				result.freeGifts = this.parseItemsCount(cartResponse) - originalItemsCount;
				if (
					result.finalPrice < originalPrice ||
					// Free gifts code is acceptable if it won't increase total
					(result.finalPrice === originalPrice && result.freeGifts > 0)
				) {
					workingDiscounts.push(result);
				}
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			});
		}).then(() => {
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			});
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;
		const { originalItemsCount } = validationInfo;

		// Sort by discount and free gifts count
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(workingDiscounts, discount => {
			// delays is needed due to blocking
			return Promise.delay(1600).then(() => {
				return this._applyCode(discount.code).then(() => {
					return Promise.delay(1600).then(() => {
						return this._getUpdatedCart().then(cartResponse => {
							if (typeof cartResponse !== 'object' && cartResponse.errors) {
								return Promise.resolve();
							}
							const finalPrice = this.parseTotal(cartResponse);
							const finalDiscount = originalPrice - finalPrice;
							const freeGifts = this.parseItemsCount(cartResponse) - originalItemsCount;

							bus.$emit('code-stacked', {
								code: discount.code,
								finalPrice,
								finalDiscount,
								freeGifts,
							});
							if (
								finalPrice < currentPrice ||
								// Check that we don't increase total with free gifts code
								(finalPrice === currentPrice && freeGifts > currentFreeGifts)
							) {
								successfulCodes.push(discount.code);
								currentDiscount = finalDiscount;
								currentPrice = finalPrice;
								currentFreeGifts = freeGifts;
								return Promise.resolve();
							}
							return this._removeCode(discount.code);
						});
					});
				});
			});
		}).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			}),
		);
	}

	applyCodes(codes) {
		if (codes && codes.length) {
			return setCookie('appliedOnCart', true, 60);
		}
		return Promise.resolve();
	}

	_manageCode(code) {
		return this._mouseEvent('mousedown')
			.then(() => {
				this.requestBodyData.set(this.CODE_PARAM_NAME, code);
				return fetch(`${this.BASE_URL}/checkout/checkout.jsp`, {
					method: 'POST',
					credentials: 'include',
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
					},
					body: this.requestBodyData,
				});
			})
			.then(response => {
				return this._mouseEvent('mouseup').then(() => {
					return response.text();
				});
			})
			.then(response => {
				// If we are blocked or session expired we could receive text
				// To prevent driver from falling we handle this case
				try {
					return JSON.parse(response);
				} catch (err) {
					return response;
				}
			});
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(cartResponse => {
			const total = this.parseTotal(cartResponse);
			const cartSummary = cartResponse.orderSummary ? cartResponse.orderSummary : cartResponse.cartSummary;
			const shipping = toFloat(cartSummary.shippingCost.replace('&#36;', '')) || 0;
			const tax = toFloat(cartSummary.tax.replace('&#36;', ''));
			return total - shipping - tax;
		});
	}

	_mouseEvent(event) {
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		codeInput.dispatchEvent(new Event(event, { bubbles: true }));
		// need to add this delay for correct site working
		return Promise.delay(1500).then(() => {
			return Promise.resolve();
		});
	}
};

export default {
	name: 'Saks Fifth Avenue',
	domain: 'saksfifthavenue.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
	maxCoupons: 3, // due to blocking
};
