import Promise from 'bluebird';
import Saks from './_base';
import { DOM, format, browserUtils } from '../../../utils';

const { getCookie } = browserUtils;
const { toFloat, elemToFloat } = format;
const { extractElement, extractFormValues, extractData } = DOM;

const BASE_URL = 'https://www.lordandtaylor.com';
const CHECKOUT_URL = `${BASE_URL}/checkout/checkout.jsp`;
const IS_ON_CHECKOUT_PAGE = window.location.href.startsWith(CHECKOUT_URL);

/* Behavior description:
- Existing code: yes
- Remove code: yes
- Stackable: no
- Free gift support: no
- Free shipping: no
- isValid implemented: yes
- Manually apply code: no
- Pages, where extension works: cart, checkout(last step)
- Is driver SPA: yes
- Gather codes on site pages: yes (/Entry.jsp page)
=======================
Special case:
	- On checkout page we use fake credit card data for applying promo code
	- maxCoupons: 4 due to blocks
*/

const Driver = class Driver extends Saks {
	constructor() {
		super();
		this.BASE_URL = BASE_URL;
		this.CART_URL = `${BASE_URL}/checkout/SaksBag.jsp`;
		this.IS_CART_PAGE = window.location.href.startsWith(this.CART_URL);
		this.CHECKOUT_URL = CHECKOUT_URL;

		this.CODE_INPUT_SELECTOR = '#promoCodeEntry';
		this.APPLY_CODE_BTN_SELECTOR = '#promoApply';
		this.REMOVE_CODE_BUTTON_SELECTOR = '.jsRemovePromo';
		this.PRODUCT_ITEM_SELECTOR = '#jsWideBag .widebag-item, .minibag-item';
		this.EXISTING_CODES_SELECTOR = '.widebag-item-promotion-codes li, .minibag-item-applied-promotion-codes';
		this.MODAL_WINDOW_SELECTOR = '#jsEditPromoCode';
		this.MODAL_CLOSE_BUTTON_SELECTOR = '.modal-close';
		this.SUBMIT_FORM_SELECTOR = '#submit_order_service';
		this.CREDIT_CARD_DATA_SELECTOR = '#paySelectCC';
		this.EDIT_BUTTON_SELECTOR = '.promotion-summary a';
		this.CODE_REGEX = /(\w+)$/;
		this.EMPTY_CART_SELECTOR = '.widebag-empty-message';
		this.REVIEW_TAB_SELECTOR = '.jsLoadReview.tab-on';
		this.PAYMENT_TAB_SELECTOR = '.promotion-summary';
		this.SPINNER_ELEMENT_SELECTOR = '.blockUI';
		this.SHIPPING_MODAL_CHECKOUT = '.update-billing-address-layer';
		this.TAX_ELEMENT_SELECTOR = '#jsVal-tax';
		this.SHIPPING_ELEMENT_SELECTOR = '#jsVal-shippingCost';
		this.SHIPPING_OPTION_SELECTOR = '#jsSelectShippingMethod option, .select-shipping-method input';
		this.SHIPPING_METHOD_SELECTOR = '#checkout-shipping-heading';
		this.MERCHANT_CODES_ELEMENT_SELECTOR = '.mini-promo, #ui-globalpromo-state';
		this.MERCHANT_CODE_REGEX = /use code.(\w+)/i;

		this.CREDIT_CARD_DATA_ARRAY = [
			{
				name: 'CREDIT_CARD<>cardBrand_cd',
				selector: '#payCC',
				value: 'MC',
			},
			{
				name: 'CREDIT_CARD<>cardNum',
				selector: '#payCCNum',
				value: '5168333333333333',
			},
			{
				name: 'CREDIT_CARD<>cardholderName',
				selector: '#payCCName',
				value: 'universal',
			},
			{
				name: 'CREDIT_CARD<>cardMonth_cd',
				selector: '#ccMonth',
				value: '9',
			},
			{
				name: 'CREDIT_CARD<>cardYear_cd',
				selector: '#ccYear',
				value: '2021',
			},
			{
				name: 'card_cvNumber',
				selector: '#payCCV',
				value: '740',
			},
		];
	}

	checkCondition() {
		const { href } = window.location;
		const country = getCookie('E4X_COUNTRY');
		const isRightCountry = country ? country === 'US' : false;
		const codeInput = extractElement(this.CODE_INPUT_SELECTOR);
		const isCartEmpty = document.querySelector(this.EMPTY_CART_SELECTOR);
		const paymentTab =
			document.querySelector(this.PAYMENT_TAB_SELECTOR) && !document.querySelector(this.SHIPPING_MODAL_CHECKOUT);
		const isReview = document.querySelector(this.REVIEW_TAB_SELECTOR);
		const isSpinner = document.querySelector(this.SPINNER_ELEMENT_SELECTOR);
		return IS_ON_CHECKOUT_PAGE
			? href.startsWith(this.CHECKOUT_URL) &&
					(paymentTab || (codeInput && (paymentTab || isReview))) &&
					!isSpinner &&
					isRightCountry
			: href.startsWith(this.CART_URL) && codeInput && !isCartEmpty && isRightCountry;
	}

	beforeTestCodes() {
		this.updateCartBody.set('bmForm', this.CART_PARAM_VALUE);
		if (document.querySelector(this.CHECKOUT_PAYMENT_FORM_SELECTOR)) {
			this.requestBodyData = extractFormValues(this.CHECKOUT_PAYMENT_FORM_SELECTOR).params;
		}
		this.CREDIT_CARD_DATA_EXISTS = !!document.querySelector(this.CREDIT_CARD_DATA_SELECTOR);
		if (IS_ON_CHECKOUT_PAGE) {
			this.CREDIT_CARD_DATA_ARRAY.map(item => this.requestBodyData.set(item.name, item.value));
		}
		return Promise.resolve();
	}

	getMerchantCodes() {
		return fetch(`${BASE_URL}/Entry.jsp`, {
			method: 'GET',
		})
			.then(response => response.text())
			.then(responseHTML => {
				return [
					...new Set(
						[...extractElement(this.MERCHANT_CODES_ELEMENT_SELECTOR, responseHTML, true)]
							.map(item => item.textContent.match(this.MERCHANT_CODE_REGEX))
							.filter(Boolean)
							.map(item => item[1]),
					),
				];
			});
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.EXISTING_CODES_SELECTOR)].map(el =>
				IS_ON_CHECKOUT_PAGE ? el.textContent.match(this.CODE_REGEX)[0] : el.textContent,
			),
		);

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	applyCodes(codes) {
		return this._applyCode(codes);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(cartResponse => {
			const total = this.parseTotal(cartResponse);
			const cartSummary = cartResponse.orderSummary ? cartResponse.orderSummary : cartResponse.cartSummary;
			const shipping = toFloat(cartSummary.shippingCost.replace('&#36;', '')) || 0;
			const tax = toFloat(cartSummary.tax.replace('&#36;', ''));
			return total - shipping - tax;
		});
	}

	_applyCode(code) {
		this.requestBodyData.set('bmForm', !IS_ON_CHECKOUT_PAGE ? 'applyPromoSaksBag' : 'add_promo_code_service');
		return this._manageCode(code);
	}

	_removeCode(code) {
		this.requestBodyData.set(
			'bmForm',
			!IS_ON_CHECKOUT_PAGE ? 'remove_promo_code_saks_bag_service' : 'remove_promo_code_service',
		);
		return this._manageCode(code);
	}

	parseTotal(data) {
		if (data) {
			let totalPrice = toFloat(
				data[IS_ON_CHECKOUT_PAGE ? 'orderSummary' : 'cartSummary'].grandTotal.replace('&#36;', ''),
			);
			const taxPrice = toFloat(
				data[IS_ON_CHECKOUT_PAGE ? 'orderSummary' : 'cartSummary'].tax.replace('&#36;', ''),
			);
			const taxOnPage = toFloat(extractData(this.TAX_ELEMENT_SELECTOR, null));
			const isShippingFree = extractData(this.SHIPPING_ELEMENT_SELECTOR, null).match(/Free/i);
			if (!IS_ON_CHECKOUT_PAGE && isShippingFree && !(taxOnPage > 0)) {
				totalPrice -= taxPrice;
			}
			return totalPrice;
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Lord & Taylor',
	domain: 'lordandtaylor.com',
	maxCoupons: 4, // due to blocks
	isSinglePageApp: true,
	Driver,
};
