import Promise from 'bluebird';
import bus from '../../bus';
import Saks from './_base';
import { format, log } from '../../../utils';
import { extractElement } from '../../../utils/DOM';
import { sessionTimer } from '../../lib';

const { toFloat } = format;

/* Driver behavior description:
- Existing code: yes
- Stackable: yes
- Free gift support: yes
- Free shipping: no
- isValid implemented: yes
- Remove code: yes
- Codes can be replaced by another code: yes (valid ones only)
- GetMerchantCodes: yes
- Supported pages: Cart, Checkout.
- Is SPA: yes
*/

const BASE_URL = 'https://www.saksoff5th.com';
const CART_URL = `${BASE_URL}/checkout/SaksBag.jsp`;
const CHECKOUT_URL = `${BASE_URL}/checkout/checkout.jsp`;
const isOnCartPage = window.location.href.includes('SaksBag.jsp');

const Driver = class Driver extends Saks {
	constructor() {
		super();
		this.TOTAL_SELECTOR = '#jsVal-grandTotal, .hbc-price-display__subtotal .hbc-price-display__summary-price';
		this.PRODUCT_ITEM_SELECTOR =
			'.widebag-item:not([id^=saveFor]), .minibag-item, .shopping-bag-item--purchase, .hbc-cart-item';
		this.bodyData = null;
		this.CODE_PARAM_NAME = 'promoCode';
		this.ADD_CODE_PARAM_VALUE = 'applyPromoSaksBag';
		this.REMOVE_CODE_PARAM_VALUE = isOnCartPage
			? 'remove_promo_code_saks_bag_service'
			: 'remove_promo_code_service';
		this.CART_PARAM_VALUE = isOnCartPage ? 'initialize_saks_bag_service' : 'continue_to_checkout_service';
		this.APPLIED_CODE_SELECTOR =
			'.jsRemovePromo .action-link-text, .widebag-item-promotion-codes li, .shopping-bag-item__promo-code, .hbc-cart-item__promo-code';
		this.CART_PARAM_VALUE = isOnCartPage ? 'initialize_saks_bag_service' : 'continue_to_checkout_service';
		this.updateCartBody = new URLSearchParams();
		this.MAX_STACKED_COUPONS = 5;
		this.CODE_INPUT_SELECTOR = '#promoCodeEntry, [name="promoCode"]';
		this.EMPTY_CART_SELECTOR = '.shopping-bag__no-items-msg';
		this.GIFT_MODAL_SELECTOR = '.gift-option-dialog-modal';
		this.SHIPPING_SECTION_SELECTOR = '.hbc-checkout__address-form-wrapper';
		this.MERCHANT_CODES_ELEMENT_SELECTOR = '#ui-globalpromo .hbc-promo-detail';
		this.MERCHANT_CODE_ELEMENT_REGEX = /code(\w+)|code (\w+)/i;
		this.MERCHANT_CODE_REGEX = /code\s?(\w+)/i;
		this.REPLACE_REGEX = /[A-z,;\s]|[&#]\d*/g;

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 45,
		});
	}

	checkCondition() {
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isCart = window.location.href.startsWith(CART_URL);
		const cartEmptyEl = document.querySelector(this.EMPTY_CART_SELECTOR);
		const isGiftModal = document.querySelector(this.GIFT_MODAL_SELECTOR);
		const isCartEmpty = cartEmptyEl && cartEmptyEl.offsetParent;
		const isCheckout = window.location.href.startsWith(CHECKOUT_URL);
		const isShippingStep = document.querySelector(this.SHIPPING_SECTION_SELECTOR);

		return !isGiftModal && isCodeInput && ((isCheckout && !isShippingStep) || (isCart && !isCartEmpty));
	}

	beforeTestCodes() {
		this.updateCartBody.set('bmForm', this.CART_PARAM_VALUE);
		this.bodyData = {
			action: CHECKOUT_URL,
			params: new URLSearchParams({
				bmForm: '',
				promoCode: '',
			}),
		};
		return Promise.resolve();
	}

	getMerchantCodes() {
		return fetch(`${BASE_URL}/Entry.jsp`, {
			method: 'GET',
		})
			.then(response => response.text())
			.then(responseHTML => {
				return [
					...new Set(
						[...extractElement(this.MERCHANT_CODES_ELEMENT_SELECTOR, responseHTML, true)]
							.map(item => item.textContent)
							.filter(item => this.MERCHANT_CODE_ELEMENT_REGEX.test(item)),
					),
				].map(item => item.match(this.MERCHANT_CODE_REGEX)[1]);
			});
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].filter(Boolean).map(el => el.textContent),
		);

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		let lastResponse = null;
		return Promise.each(codes, code =>
			this._removeCode(code).then(responseJSON => {
				lastResponse = responseJSON;
			}),
		).then(() => ({
			originalPrice: this._parseTotal(lastResponse),
			originalItemsCount: this._parseItemsCount(lastResponse),
		}));
	}

	checkCodes(codes, originalPrice = this._parseTotal(), originalItemsCount = this._parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
				isValid: false,
			};
			return this._applyCode(code).then(responseJSON => {
				if (typeof responseJSON === 'object' && !responseJSON.errors) {
					if (isOnCartPage) {
						result.finalPrice = this._parseTotal(responseJSON);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.freeGifts = this._parseItemsCount(responseJSON) - originalItemsCount;
						result.isValid = true;
						if (
							result.finalPrice < originalPrice ||
							// Free gifts code is acceptable if it won't increase total
							(result.finalPrice === originalPrice && result.freeGifts > 0)
						) {
							workingDiscounts.push(result);
						}
						return this._removeCode(code).then(() => bus.$emit('code-checked', result));
					}
					return this._getUpdatedCart().then(responseCart => {
						result.finalPrice = this._parseTotal(responseCart);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.freeGifts = this._parseItemsCount(responseCart) - originalItemsCount;
						result.isValid = true;
						if (
							result.finalPrice < originalPrice ||
							// Free gifts code is acceptable if it won't increase total
							(result.finalPrice === originalPrice && result.freeGifts > 0)
						) {
							workingDiscounts.push(result);
						}
						return this._removeCode(code).then(() => bus.$emit('code-checked', result));
					});
				}
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			});
		}).then(() =>
			bus.$emit('stack-codes', {
				codes: workingDiscounts.map(discount => discount.code),
				discounts: workingDiscounts,
				originalPrice,
				originalItemsCount,
			}),
		);
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;
		const { originalItemsCount } = validationInfo;

		// Sort by discount and free gifts count
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(workingDiscounts, discount =>
			this._applyCode(discount.code).then(responseJSON => {
				if (successfulCodes.length >= this.MAX_STACKED_COUPONS) {
					return this._removeCode(discount.code).then(() =>
						bus.$emit('code-stacked', {
							code: discount.code,
							finalPrice: originalPrice,
							finalDiscount: 0,
							freeGifts: 0,
							isValid: false,
						}),
					);
				}
				if (typeof responseJSON !== 'object' || responseJSON.errors) {
					return this._removeCode(discount.code).then(() =>
						bus.$emit('code-stacked', {
							code: discount.code,
							finalPrice: originalPrice,
							finalDiscount: 0,
							freeGifts: 0,
							isValid: false,
						}),
					);
				}
				if (isOnCartPage) {
					const finalPrice = this._parseTotal(responseJSON);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this._parseItemsCount(responseJSON) - originalItemsCount;
					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});
					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this._removeCode(discount.code);
				}
				return this._getUpdatedCart().then(responseCart => {
					const finalPrice = this._parseTotal(responseCart);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this._parseItemsCount(responseCart) - originalItemsCount;
					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});
					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this._removeCode(discount.code);
				});
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			}),
		);
	}

	getPreTaxShippingTotal() {
		return Promise.delay(3000).then(() =>
			this._getUpdatedCart().then(responseJSON => {
				const { shippingCost, tax } = responseJSON.orderSummary
					? responseJSON.orderSummary
					: responseJSON.cartSummary;
				const total = this._parseTotal(responseJSON);
				const shippingAmount =
					shippingCost.toLowerCase() === 'free' || shippingCost.toLowerCase() === 'tbd'
						? 0
						: toFloat(shippingCost.replace(this.REPLACE_REGEX, ''));
				const taxAmount = toFloat(tax.toLowerCase() === 'tbd' ? 0 : tax.replace(this.REPLACE_REGEX, ''));
				return total - shippingAmount - taxAmount;
			}),
		);
	}

	_applyCode(code) {
		this.bodyData.params.set('bmForm', this.ADD_CODE_PARAM_VALUE);
		return this._manageCode(code);
	}

	_removeCode(code) {
		this.bodyData.params.set('bmForm', this.REMOVE_CODE_PARAM_VALUE);
		return this._manageCode(code);
	}

	_manageCode(code) {
		this.bodyData.params.set(this.CODE_PARAM_NAME, code);
		return fetch(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
			},
			body: this.bodyData.params,
		})
			.then(response => response.text())
			.then(response => {
				// If we are blocked or session expired we could receive text
				// To prevent driver from falling we handle this case
				try {
					return JSON.parse(response);
				} catch (err) {
					return response;
				}
			});
	}

	_getUpdatedCart() {
		return fetch(CHECKOUT_URL, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
				'x-requested-with': 'XMLHttpRequest',
			},
			body: this.updateCartBody,
		}).then(response => response.json());
	}

	_parseTotal(data = false) {
		let total = null;
		if (data) {
			// Replace dollar sign
			const summary = data.orderSummary ? data.orderSummary : data.cartSummary;
			total = toFloat(summary.grandTotal.replace(this.REPLACE_REGEX, '').trim());
		} else {
			total = toFloat(
				document
					.querySelector(this.TOTAL_SELECTOR)
					.textContent.replace(this.REPLACE_REGEX, '')
					.trim(),
			);
		}
		return total;
	}

	_parseItemsCount(data) {
		if (data) {
			return data.cartItems ? data.cartItems.length : data.orderItems.length;
		}
		return [...document.querySelectorAll(this.PRODUCT_ITEM_SELECTOR)].length;
	}
};

export default {
	name: 'Saks Fifth Avenue Off Fifth',
	domain: 'saksoff5th.com',
	Driver,
	stackable: true,
	isSinglePageApp: true,
};
