import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, log, browserUtils } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { wait } = browserUtils;
const { extractFormValues, setValue, extractElement } = DOM;

/* Behaviour
 * Saks sites sometimes have two different design
 * For now, saksfifthavenue and saksoff5th have new design
 * lordandtaylor has old design
 * Base driver has logic for `old design`
 * Difference:
 * - selectors
 * - event in `setValue`
 * - watchForCartUrl
 */

export default class Saks extends BaseDriver {
	constructor() {
		super();
		this.BASE_URL = '';
		this.TOTAL_SELECTOR = '#jsVal-grandTotal, .hbc-price-display__subtotal .hbc-price-display__summary-price';
		this.APPLIED_CODE_SELECTOR =
			'.jsRemovePromo .action-link-text, .widebag-item-promotion-codes li, .shopping-bag-item__promo-code, .hbc-cart-item__promo-code';
		this.CODE_INPUT_SELECTOR = '#promoCodeEntry, [name="promoCode"]';
		this.APPLY_BUTTON_SELECTOR = '#promoApply, .hbc-promo-code__submit-btn';

		this.CODE_PARAM_NAME = 'promoCode';

		this.CHECKOUT_PAYMENT_FORM_SELECTOR = '#submit_payment_service';
		this.PRODUCT_ITEM_SELECTOR =
			'.widebag-item:not([id^=saveFor]), .minibag-item, .shopping-bag-item--purchase, .hbc-cart-item';
		this.EDIT_BUTTON_SELECTOR = '.promotion-summary a';
		this.MODAL_WINDOW_SELECTOR = '#jsEditPromoCode';
		this.MODAL_CLOSE_BUTTON_SELECTOR = '.modal-close';

		const isOnCartPage = window.location.href.includes('SaksBag.jsp');
		this.ADD_CODE_PARAM_VALUE = 'applyPromoSaksBag';
		this.REMOVE_CODE_PARAM_VALUE = isOnCartPage
			? 'remove_promo_code_saks_bag_service'
			: 'remove_promo_code_service';
		this.CART_PARAM_VALUE = isOnCartPage ? 'initialize_saks_bag_service' : 'continue_to_checkout_service';

		this.MAX_STACKED_COUPONS = 5;

		this.isStackable = false;

		this.requestBodyData = new URLSearchParams();
		this.updateCartBody = new URLSearchParams();
	}

	beforeTestCodes() {
		this.updateCartBody.set('bmForm', this.CART_PARAM_VALUE);
		if (document.querySelector(this.CHECKOUT_PAYMENT_FORM_SELECTOR)) {
			this.requestBodyData = extractFormValues(this.CHECKOUT_PAYMENT_FORM_SELECTOR).params;
		}
		return Promise.resolve();
	}

	checkCondition() {
		const isCodeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const isEditPromoButton = document.querySelector(this.EDIT_BUTTON_SELECTOR);
		const isCart =
			window.location.href.startsWith(this.CART_URL) && document.querySelector(this.CODE_INPUT_SELECTOR);
		const isCheckout = window.location.href.startsWith(this.CHECKOUT_URL);
		const isPaymentFilled =
			this._isPaymentFilled(`${this.CHECKOUT_PAYMENT_FORM_SELECTOR} input`) &&
			this._isPaymentFilled(`${this.CHECKOUT_PAYMENT_FORM_SELECTOR} select`);

		return (isCart && isCodeInput) || (isCheckout && ((isCodeInput && isPaymentFilled) || isEditPromoButton));
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].filter(Boolean).map(el => el.textContent),
		);

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes(codes) {
		return Promise.each(codes, code => this._removeCode(code))
			.then(() => this._getUpdatedCart())
			.then(responseJSON => ({
				originalPrice: this.parseTotal(responseJSON),
				originalItemsCount: this.parseItemsCount(responseJSON),
			}));
	}

	applyCodes(codes) {
		return this.IS_CART_PAGE ? this.applyCodesOnCart(codes[0]) : this.applyCodesOnCheckout(codes[0]);
	}

	applyCodesOnCart(code) {
		return this.isStackable ? Promise.resolve() : this._applyCode(code);
	}

	applyCodesOnCheckout(code) {
		if (!document.querySelector(this.EDIT_BUTTON_SELECTOR)) {
			if (this.IS_CART_PAGE) {
				return Promise.resolve();
			}
			return this._removeCode(code).then(() => this._actionWithPromoElements(code));
		}
		const isModalWindowOnPage = () => !!document.querySelector(this.MODAL_WINDOW_SELECTOR);
		document.querySelector(this.EDIT_BUTTON_SELECTOR).click();
		return wait(isModalWindowOnPage)
			.then(() => this._removeCode(code).then(() => this._actionWithPromoElements(code)))
			.then(() => {
				if (document.querySelector(this.MODAL_CLOSE_BUTTON_SELECTOR)) {
					document.querySelector(this.MODAL_CLOSE_BUTTON_SELECTOR).click();
				}
				return Promise.resolve();
			});
	}

	checkCodes(codes, originalPrice = this.parseTotal(), originalItemsCount = this.parseItemsCount()) {
		log('originalPrice: %s', originalPrice);
		log('originalItemsCount: %s', originalItemsCount);

		const workingDiscounts = [];
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				freeGifts: 0,
				isValid: false,
			};
			return this._applyCode(code).then(responseJSON => {
				if (typeof responseJSON === 'object' && !responseJSON.errors) {
					return this._getUpdatedCart().then(cartResponse => {
						result.finalPrice = this.parseTotal(cartResponse);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.freeGifts = this.parseItemsCount(cartResponse) - originalItemsCount;
						result.isValid = true;
						if (
							result.finalPrice < originalPrice ||
							// Free gifts code is acceptable if it won't increase total
							(result.finalPrice === originalPrice && result.freeGifts > 0)
						) {
							workingDiscounts.push(result);
						}
						return this._removeCode(code).then(() => bus.$emit('code-checked', result));
					});
				}
				return bus.$emit('code-checked', result);
			});
		}).then(() => {
			if (this.isStackable) {
				bus.$emit('stack-codes', {
					codes: workingDiscounts.map(discount => discount.code),
					discounts: workingDiscounts,
					originalPrice,
					originalItemsCount,
				});
			}
			return Promise.resolve();
		});
	}

	stackCodes(validationInfo) {
		const workingDiscounts = validationInfo.discounts;
		const { originalPrice } = validationInfo;
		const { originalItemsCount } = validationInfo;

		// Sort by discount and free gifts count
		workingDiscounts.sort((a, b) => {
			if (b.finalDiscount - a.finalDiscount === 0) {
				return b.freeGifts - a.freeGifts;
			}
			return b.finalDiscount - a.finalDiscount;
		});

		let currentPrice = originalPrice;
		let currentDiscount = 0;
		let currentFreeGifts = 0;
		const successfulCodes = [];

		return Promise.each(workingDiscounts, discount =>
			this._applyCode(discount.code).then(() =>
				this._getUpdatedCart().then(cartResponse => {
					const finalPrice = this.parseTotal(cartResponse);
					const finalDiscount = originalPrice - finalPrice;
					const freeGifts = this.parseItemsCount(cartResponse) - originalItemsCount;

					bus.$emit('code-stacked', {
						code: discount.code,
						finalPrice,
						finalDiscount,
						freeGifts,
					});
					if (successfulCodes.length >= this.MAX_STACKED_COUPONS) {
						return this.removeCode(discount.code);
					}
					if (
						finalPrice < currentPrice ||
						// Check that we don't increase total with free gifts code
						(finalPrice === currentPrice && freeGifts > currentFreeGifts)
					) {
						successfulCodes.push(discount.code);
						currentDiscount = finalDiscount;
						currentPrice = finalPrice;
						currentFreeGifts = freeGifts;
						return Promise.resolve();
					}
					return this._removeCode(discount.code);
				}),
			),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
				freeGifts: currentFreeGifts,
			}),
		);
	}

	_applyCode(code) {
		this.requestBodyData.set('bmForm', this.ADD_CODE_PARAM_VALUE);
		return this._manageCode(code);
	}

	_removeCode(code) {
		this.requestBodyData.set('bmForm', this.REMOVE_CODE_PARAM_VALUE);
		return this._manageCode(code);
	}

	_manageCode(code) {
		this.requestBodyData.set(this.CODE_PARAM_NAME, code);
		return fetch(`${this.BASE_URL}/checkout/checkout.jsp`, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
			},
			body: this.requestBodyData,
		})
			.then(response => response.text())
			.then(response => {
				// If we are blocked or session expired we could receive text
				// To prevent driver from falling we handle this case
				try {
					return JSON.parse(response);
				} catch (err) {
					return response;
				}
			});
	}

	_isPaymentFilled(selector) {
		const data = [...document.querySelectorAll(selector)].filter(el => el.offsetParent !== null);
		if (data.length > 0) {
			return data.length === data.filter(item => item.value !== '').length;
		}
		if (document.querySelector(selector)) {
			return true;
		}
		return false;
	}

	_actionWithPromoElements(code) {
		const codeInput = extractElement(this.CODE_INPUT_SELECTOR);
		const applyButton = extractElement(this.APPLY_BUTTON_SELECTOR);
		setValue(codeInput, code, 'input');
		setValue(codeInput, code, 'blur');
		applyButton.click();
		return Promise.resolve();
	}

	_getUpdatedCart() {
		return fetch(this.IS_CART_PAGE ? this.CART_URL : this.CHECKOUT_URL, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
				'x-requested-with': 'XMLHttpRequest',
			},
			body: this.updateCartBody,
		}).then(response => response.json());
	}

	parseTotal(data) {
		let total = null;
		if (data) {
			// Replace dollar sign
			const summary = data.orderSummary ? data.orderSummary : data.cartSummary;
			total = toFloat(summary.grandTotal.replace('&#36;', ''));
		} else {
			total = elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
		}
		return total;
	}

	parseItemsCount(data) {
		if (data) {
			return data.cartItems ? data.cartItems.length : data.orderItems.length;
		}
		return [...document.querySelectorAll(this.PRODUCT_ITEM_SELECTOR)].length;
	}
}
